body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.App {
  text-align: center;
  margin: 0px auto;
  max-width: 860px;
  border: 1px solid black;
  padding: 0 10px;
}

h1, h2, h3, h4, h5, h6 {
  margin: 0;
}

.app-header {
  display: -webkit-flex;
  display: flex;
  -webkit-justify-content: flex-start;
          justify-content: flex-start;
  margin-bottom: 2%;
  border-bottom: 1px solid lightgray;
  padding: 7px 0;
}

.appBody {
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: row;
          flex-direction: row;
  -webkit-justify-content: flex-start;
          justify-content: flex-start;
  width: 100%;
  border: 1px solid gray;
  background: rgb(241, 240, 240);
}

.app-menu {
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: column;
          flex-direction: column;
  -webkit-align-items: center;
          align-items: center;
  text-align: left;
  background-color: rgb(216, 216, 216);
  color: black;
  min-height: 100vh;
  width: 20%;
  padding: 2%;
}

.app-menu h1 {
  margin-top: 10px;
  font-size: 27px;
}

.note-menu {
  display: -webkit-flex;
  display: flex;
  -webkit-justify-content: flex-end;
          justify-content: flex-end;
  margin-top: 10px; 
}

.note-list {
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: row;
          flex-direction: row;
  -webkit-flex-wrap: wrap;
          flex-wrap: wrap;
  -webkit-justify-content: space-between;
          justify-content: space-between;
}

.menu-buttons {
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: column;
          flex-direction: column;
  -webkit-align-items: center;
          align-items: center;
  margin-top: 5px;
}

.menu-button {
  padding-bottom: 13px;
}

.note {
  margin-bottom: 10px;
  background: white;
  border: 1px solid gray;
  width: 24%;
  padding: 1% 3%;
  overflow: hidden;
  margin-bottom: 6%;
  margin-right: 2%;
}

.note-title {
  border-bottom: 2px solid lightgray;
  padding-bottom: 2%;
  text-transform: uppercase;
}

.note-title-single {
  padding-bottom: 2%;
  text-transform: uppercase;
}

h1 {
  margin-bottom: 15px;
  font-size: 30px;
  line-height: .8;
}

h4 {
  max-width: 200px;
  white-space: nowrap;
  overflow: hidden;
  text-overflow: ellipsis;
}

.page-header {
  margin-bottom: 6%;
}

.page-header h3 {
  text-align: left;
  font-size: 17px;
}

.input {
  width: 60%;
  margin: 5% 0;
  padding: 1.5%;
  border-radius: 2px;
  border: 2px solid rgb(194, 194, 194);
}

.text-area {
  padding: 1.5%;
  border-radius: 2px;
  border: 2px solid rgb(194, 194, 194);
  resize: none;
  width: 95%;
}

.menu-item {
  margin-left: 20px;
  font-size: 12px;
  text-decoration: underline;
  color: blue;
}
.mm-popup {
    display: none;
}

.mm-popup--visible {
    display: block;
}

.mm-popup__overlay {
    position: fixed;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    z-index: 1000;
    overflow: auto;
    background: rgba(0, 0, 0, .1);
}

.mm-popup__close {
    position: absolute;
    top: 15px;
    right: 20px;
    padding: 0;
    width: 20px;
    height: 20px;
    cursor: pointer;
    outline: none;
    text-align: center;
    border-radius: 10px;
    border: none;
    text-indent: -9999px;
    background-size: 100%;
    margin: 0;
}

.mm-popup__btn--success {
    background-color: turquoise;
    border-color: lightgray;
    color: #fff;
}

.mm-popup__btn--danger {
    background-color: #c5545c;
    border-color: lightgray;
    color: #fff;
}

.mm-popup__input {
    display: block;
    width: 100%;
    height: 30px;
    border-radius: 3px;
    background: #f5f5f5;
    border: 1px solid #e9ebec;
    outline: none;
    font-size: 14px;
    padding: 0 12px;
    color: #808080;
}

.mm-popup__btn {
    border-radius: 3px;
    padding: 0 10px;
    margin: 0;
    line-height: 32px;
    height: 32px;
    width: 175px;
    border: 1px solid #666;
    text-align: center;
    display: inline-block;
    font-size: 12px;
    font-weight: 400;
    color: white;
    outline: none;
    text-decoration: none;
    cursor: pointer;
    font-family: "Open Sans", sans-serif;
}

.mm-popup__box {
    width: 31%;
    position: fixed;
    top: 27.6%;
    left: 50%;
    margin-left: -14.5%;
    background: #fff;
    box-shadow: 0px 5px 20px 0px rgba(126,137,140,0.20);
    border-radius: 5px;
    border: 1px solid #B8C8CC;
    overflow: hidden;
    z-index: 1001;
}

.mm-popup__box__header {
    padding: 15px 20px;
    background: #EDF5F7;
    color: #454B4D;
}

.mm-popup__box__header__title {
    margin: 0;
    font-size: 16px;
    text-align: center;
    font-weight: 600;
}

.mm-popup__box__body {
    padding: 20px;
    line-height: 1.4;
    font-size: 14px;
    color: #454B4D;
    background: #fff;
    position: relative;
    z-index: 2;
    text-align: center;
}

.mm-popup__box__body p {
    margin: 0 0 5px;
}

.mm-popup__box__footer {
    overflow: hidden;
    padding: 40px 50px 20px;
}

.mm-popup__box__footer__right-space {
    float: right;
}

.mm-popup__box__footer__right-space .mm-popup__btn {
    margin-left: 5px;
}

.mm-popup__box__footer__left-space {
    float: left;
}

.mm-popup__box__footer__left-space .mm-popup__btn {
    margin-right: 5px;
}

.mm-popup__box--popover {
    width: 300px;
    margin-left: -150px;
}

.mm-popup__box--popover .mm-popup__close {
    position: absolute;
    top: 5px;
    right: 5px;
    padding: 0;
    width: 20px;
    height: 20px;
    cursor: pointer;
    outline: none;
    text-align: center;
    border-radius: 10px;
    border: none;
    text-indent: -9999px;
    margin: 0;
    z-index: 3;
}

.mm-popup__box--popover .mm-popup__box__body {
    padding: 20px;
}
